import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";

import {mapState} from "vuex";

export default {
    name: "about",
    components: {
        "app-tab-nav": appTabNav,
    },
    data() {
        return {
            activeTab: 2,
            order_list: [
                {id: 2, name: '未核销'},
                {id: 1, name: '已核销'},
                {id: 5, name: '已退款'},
                {id: 3, name: '我的'},
            ],
            card_list: [
                {id: 2, name: '未核销'},
                {id: 1, name: '已核销'},
                {id: 3, name: '我的'},
            ],
            tabList: [],
            candidate: [],
            status: 1,
            placeHeight: 312,
            setTop: 190,
            more_list: false,
            is_mall: false,
            toSearch: false,
            searchResult: false,
            first: false,
            keyword: '',
            list: []
        };
    },
    computed: {
        ...mapState({
            clerkImg: state => state.mallConfig.__wxapp_img.clerk,
            mall: state => state.mallConfig.mall
        }),
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.status = options.status
        if (options.type > 0) {
            this.activeTab = options.type
        }
        this.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        this.$request({
            url: this.$api.clerk.info
        }).then(response => {
            if (response.code == 0) {
                this.is_mall = response.data.is_mall;
                if (!response.data.is_mall) {
                    this.placeHeight = 210;
                    this.setTop = 88;
                }
                this.list = [];
                if (this.status == 1) {
                    this.tabList = this.order_list;
                    this.getList();
                    uni.setNavigationBarTitle({
                        title: "订单",
                    })
                } else if (this.status == 2) {
                    this.tabList = this.card_list;
                    this.getCard();
                    uni.setNavigationBarTitle({
                        title: "卡券",
                    })
                }
            } else {
                uni.redirectTo({
                    url: '/pages/user-center/user-center'
                })
            }
        })
    },
    onReachBottom: function () {
        if (this.more_list) {
            this.getMore();
        }
    },
    onShow(options) {
        let that = this;
        if (that.first) {
            that.$request({
                url: that.$api.clerk.info
            }).then(response => {
                if (response.code == 0) {
                    that.is_mall = response.data.is_mall;
                    if (!response.data.is_mall) {
                        this.placeHeight = 210
                    }
                    if (that.status == 1) {
                        that.getList();
                        uni.setNavigationBarTitle({
                            title: "订单",
                        })
                    } else if (that.status == 2) {
                        that.getCard();
                        uni.setNavigationBarTitle({
                            title: "卡券",
                        })
                    }
                } else {
                    uni.redirectTo({
                        url: '/pages/user-center/user-center'
                    })
                }
            })
        }

        if (!that.first) {
            if (that.status == 1) {
                that.getList();
            } else if (that.status == 2) {
                that.getCard();
            }
        }
    },
    methods: {
        getMore() {
            let that = this;
            let url;
            let para;
            if (that.load) {
                return false
            }
            that.load = true;
            if (that.status == 1) {
                if (that.activeTab == 3) {
                    url = that.$api.clerk.my;
                    para = {
                        page: that.page,
                    }
                } else if (that.activeTab == 5) {
                    url = that.$api.clerk.order;
                    para = {
                        status: that.activeTab,
                        keyword_1: 8,
                        keyword: that.keyword
                    }
                } else {
                    url = that.$api.clerk.order;
                    para = {
                        page: that.page,
                        is_clerk: that.activeTab,
                    }
                }
            } else {
                if (that.activeTab == 3) {
                    url = that.$api.clerk.my_card;
                    para = {
                        page: that.page,
                    }
                } else {
                    url = that.$api.clerk.card;
                    para = {
                        page: that.page,
                        is_clerk: that.activeTab,
                    }
                }
            }
            that.$request({
                url: url,
                data: para,
            }).then(response => {
                that.$hideLoading();
                that.load = false;
                if (response.code == 0) {
                    that.list = that.list.concat(response.data.list);
                    that.page++;
                    that.more_list = false;
                    if (response.data.list.length == response.data.pagination.pageSize) {
                        that.more_list = true;
                    }
                    that.list.forEach(function (row) {
                        row.refund = null;
                        if (row.cancel_status == 2) {
                            row.refund = '申请取消中'
                        } else if (row.is_pay == 0) {
                            row.refund = '订单未支付'
                        }
                    })
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.load = false;
                that.$hideLoading();
            });
        },

        toClerk(id) {
            uni.navigateTo({
                url: '/pages/order/clerk/clerk?id=' + id
            });
        },
        toClerkCard(id) {
            uni.navigateTo({
                url: '/pages/card/clerk/clerk?cardId=' + id
            });
        },
        toDetail(id) {
            uni.navigateTo({
                url: '/plugins/clerk/detail/detail?id=' + id
            });
        },
        toCardDetail(id) {
            uni.navigateTo({
                url: '/plugins/clerk/detail/detail?card_id=' + id
            });
        },
        keywordSearch(e) {
            this.keyword = e;
            this.searchResult = true;
            if (this.status == 1) {
                this.getList();
            } else {
                this.getCard();
            }
        },
        beSearch() {
            this.candidate = this.$storage.getStorageSync('clerk_keyword') ? this.$storage.getStorageSync('clerk_keyword') : [];
            if (this.status == 2) {
                this.candidate = this.$storage.getStorageSync('clerk_card_keyword') ? this.$storage.getStorageSync('clerk_card_keyword') : [];
            }
            this.toSearch = !this.toSearch;
            this.list = [];
            this.searchResult = false;
            uni.setNavigationBarTitle({
                title: "搜索",
            })
        },
        cancelSeacrch() {
            this.toSearch = !this.toSearch;
            this.keyword = '';
            if (this.status == 1) {
                this.getList();
                uni.setNavigationBarTitle({
                    title: "订单",
                })
            } else {
                this.getCard();
                uni.setNavigationBarTitle({
                    title: "卡券",
                })
            }
        },
        search() {
            let value = this.$storage.getStorageSync('clerk_keyword') ? this.$storage.getStorageSync('clerk_keyword') : [];
            if (this.status == 2) {
                value = this.$storage.getStorageSync('clerk_card_keyword') ? this.$storage.getStorageSync('clerk_card_keyword') : [];
            }
            if (this.keyword.length == 0 || this.keyword.trim().length == 0) {
                this.keyword = ''
                return
            } else if (value.length > 0) {
                value.unshift(this.keyword)
            } else {
                value = [this.keyword]
            }
            value.forEach(function (row, index) {
                if (value[0] == value[index] && index > 0) {
                    value.splice(index, 1)
                }
            })
            if (this.status == 1) {
                this.$storage.setStorageSync('clerk_keyword', value);
                this.getList();
            } else {
                this.$storage.setStorageSync('clerk_card_keyword', value);
                this.getCard();
            }
        },
        // 清除搜索记录
        clear() {
            let that = this;
            if(this.status == 2) {
                this.$storage.removeStorageSync('clerk_card_keyword');
            }else {
                this.$storage.removeStorageSync('clerk_keyword');
            }
        },
        tabStatus(e) {
            let that = this;
            if (that.load) {
                return false
            }
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.list = [];
            that.page = 2;
            that.activeTab = +e.currentTarget.dataset.id;
            if (this.status == 1) {
                this.getList();
            } else {
                this.getCard();
            }
        },
        tab(e) {
            let that = this;
            if (that.load) {
                return false
            }
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.list = [];
            that.page = 2;
            that.activeTab = 2;
            that.status = e;
            if (e == 1) {
                this.tabList = this.order_list;
                this.getList();
            } else {
                this.tabList = this.card_list;
                this.getCard();
            }
        },
        getList() {
            let that = this;
            let url;
            let para;
            if (that.load) {
                return false
            }
            that.load = true;
            if (this.activeTab == 3) {
                url = that.$api.clerk.my;
                para = {
                    keyword_1: 8,
                    keyword: this.keyword
                }
            } else if (this.activeTab == 5) {
                url = that.$api.clerk.order;
                para = {
                    status: this.activeTab,
                    keyword_1: 8,
                    keyword: this.keyword
                }
            } else {
                url = that.$api.clerk.order;
                para = {
                    is_clerk: this.activeTab,
                    keyword_1: 8,
                    keyword: this.keyword
                }
            }
            that.$request({
                url: url,
                data: para,
            }).then(response => {
                uni.hideLoading();
                that.$hideLoading();
                that.load = false;
                if (response.code == 0) {
                    that.first = true;
                    that.list = response.data.list;
                    that.page = 2;
                    that.searchResult = true;
                    that.more_list = false;
                    if (response.data.list.length == response.data.pagination.pageSize) {
                        that.more_list = true;
                    }
                    that.list.forEach(function (row) {
                        row.refund = null;
                        if (row.cancel_status == 2) {
                            row.refund = '申请退款中'
                        } else if (row.is_pay == 0) {
                            row.refund = '订单未支付'
                        }
                    })
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.load = false;
                uni.hideLoading();
                that.$hideLoading();
            });
        },
        getCard() {
            let that = this;
            let url;
            let para;
            if (that.load) {
                return false
            }
            that.load = true;
            if (this.activeTab == 3) {
                url = that.$api.clerk.my_card;
                para = {
                    keyword: this.keyword
                }
            } else {
                url = that.$api.clerk.card;
                para = {
                    is_clerk: this.activeTab,
                    keyword: this.keyword
                }
            }
            that.$request({
                url: url,
                data: para,
            }).then(response => {
                that.load = false;
                uni.hideLoading();
                that.$hideLoading();
                if (response.code == 0) {
                    that.list = response.data.list;
                    that.page = 2;
                    that.searchResult = true;
                    that.more_list = false;
                    if (response.data.list.length == response.data.pagination.pageSize) {
                        that.more_list = true;
                    }
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.load = false;
                uni.hideLoading();
                that.$hideLoading();
            });
        },
    }
}
